<template>
  <div class="hc-storeList">
     <mt-header class="hc-header" fixed title="匹配门店列表">
        <mt-button @click="back" slot="left" icon="back"></mt-button>
      </mt-header>
      <div class="hc-content">
        <div class="hc-storeList-item" v-for="(item, index) in result.list" :key="index">
          <h3>{{item.storeName}}</h3>
          <div class="hc-flex">
              <div class="hc-flex-item">
                  <div class='hc-icon' v-bind:style ="{backgroundImage:'url('+item.imgUrl+')'}"></div>
              </div>
              <div class="hc-flex-item hc-flex-item-5">
                  <h2>{{item.adress}}</h2>
                  <span>距离我 <span class="address">{{item.distance}}km</span> <icon value='lujing6'></icon> </span>
              </div>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import { getStoreBylatAndIng } from '../../api/axios.js'
export default {
  name:'store',
  data(){
    return {
      score:9,
      result:{}
    }
  },
  components:{
  },
  methods:{
    handleItem(item){
      this.result.categoryId = item.id;
      // this.getProductList();
    },
    getProductList(){
      getStoreBylatAndIng().then((data) => {
        console.info(data)
        this.result = data.result;
      })
    }
  },
  mounted(){
    this.getProductList({storeId:'1'})
  }
}
</script>
<style lang="scss">
.hc-header {
    height: px2rem(48);
    font-size: px2rem(16);
    .mint-header-button{
        -webkit-box-flex: .1;
        -ms-flex: .1;
        flex: .1;
    }
    .mintui{
        font-size: px2rem(16);
    }
}
.hc-storeList{
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  .hc-content{
    background: #fff;
  }
  .hc-storeList-item{
    height: px2rem(105);
    border-bottom: 1px solid #dcdcdc;
    margin: 0 px2rem(30);
    padding: px2rem(15) 0;
    h3{
       font-size: px2rem(16);
       height: px2rem(27);
       line-height: px2rem(27);
       color: #3a3533;
    }
    h2,span{
      color: #b3b3b3;
      font-size: px2rem(12);
    }
    .address{
      color: #ff7371;
    }
    .hc-flex{
        .hc-icon{
          width: px2rem(40);
          height: px2rem(40);
          border-radius: 100%;
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          display: inline-block;
        }
    }
  }
}
</style>



